const hasTargetAndMessage = (card) => Boolean(card.extras.message) &&
    Boolean(card.extras.target) &&
    Boolean(card.extras.ophanLabel);
export const mapBrazeCardsToNotifications = (cards) => {
    return cards.filter(hasTargetAndMessage).map((card) => {
        return {
            id: card.id,
            target: card.extras.target,
            message: card.extras.message,
            ophanLabel: card.extras.ophanLabel,
            logImpression: () => {
                card.logImpression();
            },
        };
    });
};
const groupNotificationsByTarget = (notifications) => {
    return notifications.reduce((groupings, notification) => {
        groupings[notification.target] = (groupings[notification.target] ?? []).concat(notification);
        return groupings;
    }, {});
};
export const addNotificationsToDropdownLinks = (links, notifications) => {
    const notificationsByTarget = groupNotificationsByTarget(notifications);
    const linksWithNotifications = links.map((link) => {
        const newNotifications = (link.notifications ?? []).concat(notificationsByTarget[link.id] ?? []);
        if (newNotifications.length === 0) {
            return link;
        }
        return {
            ...link,
            notifications: newNotifications,
        };
    });
    return linksWithNotifications;
};
